import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "all",
    component: () =>
      import(/* webpackChunkName: "estrenos" */ "../views/All.vue")
  },
  {
    path: "/estrenos",
    name: "estrenos",
    component: () =>
      import(/* webpackChunkName: "estrenos" */ "../views/Estrenos.vue")
  },
  {
    path: "/generos/:id",
    name: "Generos",
    component: () =>
      import(/* webpackChunkName: "generos" */ "../views/Generos.vue")
  },
  {
    path: "/descripcion/:id",
    name: "Descripcion",
    component: () =>
      import(/* webpackChunkName: "descripcion" */ "../views/Descripcion.vue")
  },
  {
    path: "/years/:id",
    name: "Years",
    component: () =>
      import(/* webpackChunkName: "years" */ "../views/Years.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
