import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    titulo: 'Trailers',
    movies: [],
  },
  mutations: {
    llenarPeliculas(state,moviesAccion){
      state.movies = moviesAccion;
    }
   
  },
  actions: {
    obtenerMovie: async function ({ commit }) {
      

        const data = await fetch('https://ailenariosto.github.io/movies/peliculas.json');
        const movies = await data.json();
        commit('llenarPeliculas', movies);
    }
    
  },
  modules: {
  }
})
